<template>
    <div class="tile is-parent">
        <div class="tile is-child box">
            <div v-if="course" @input="newInput">
                <div class="field">
                    <div class="control">
                        <input class="input" type="text" placeholder="Course Name" v-model="data.name">
                    </div>
                </div>
                <table class="table is-narrow">
                    <thead>
                    <tr>
                        <th>Assignment</th>
                        <th>Grade</th>
                        <th>Weight</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="assignment in course.assignments">
                        <td>
                            <div class="field">
                                <div class="control">
                                    <input class="input is-small" type="text" placeholder="Name"
                                           v-model="assignment.name">
                                </div>
                            </div>
                        </td>
                        <td>
                            <div class="field">
                                <div class="control">
                                    <input class="input is-small" type="number" v-model="assignment.grade">
                                </div>
                            </div>
                        </td>
                        <td>
                            <div class="field">
                                <div class="control">
                                    <input class="input is-small" type="number" v-model="assignment.weight">
                                </div>
                            </div>
                        </td>
                    </tr>
                    </tbody>
                </table>
                <button class="button is-small is-primary" @click="add">Add Assignment</button>
                <button class="button is-small is-danger" @click="remove">Remove last row</button>
                <button class="button is-small is-danger" @click="deleteCourse">Remove this course from the list</button>
                <hr>
                <div class="columns">
                    <div class="column">
                        <div class="field">
                            <div class="control">
                                <label>What is your desired grade?</label>
                                <input class="input" type="number" placeholder="Grade (%)" v-model="remaining">
                            </div>
                        </div>
                        <p v-if="remaining">
                            To end up with a grade of {{ remaining }}%, you will need {{ remainingAssignments }}% on the
                            remaining assignments.
                        </p>
                    </div>
                    <div class="column">
                        <div class="field">
                            <div class="control">
                                <label>Prediction on the future assignments</label>
                                <input class="input" type="number" placeholder="Grade (%)" v-model="prediction">
                            </div>
                        </div>
                        <p v-if="prediction">
                            Your final grade is gonna be <strong>{{ finalGrade }}</strong> if you get {{ prediction }}% on
                            the remaining assignments
                        </p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
  /* eslint-disable */
  export default {
    name: "course",
    props: {
      data: {
        required: true,
        type: Object
      }
    },
    data() {
      return {
        course: null,
        prediction: null,
        remaining: null
      }
    },
    computed: {
      finalGrade() {
        let average = this.average()
        average += parseFloat(this.prediction) * (100 - this.weight()) / 100
        return average.toFixed(2)
      },
      remainingAssignments() {
        return ((this.remaining - this.average()) * 100 / this.weight()).toFixed(2)
      }
    },
    mounted() {
      this.course = this.data
    },
    methods: {
      average(){
        let average = 0
        for (let assignment in this.course.assignments) {
          let info = this.course.assignments[assignment]
          if (info.grade !== '' && info.weight !== '') {
            average += (parseFloat(info.grade) * parseFloat(info.weight)) / 100
          }
        }
        return average
      },
      weight(){
        let weight = 0
        for (let assignment in this.course.assignments) {
          let info = this.course.assignments[assignment]
          if (info.grade !== '' && info.weight !== '') {
            weight += parseFloat(info.weight)
          }
        }
        return weight
      },
      add(){
        this.course.assignments.push({
          name: '',
          grade: '',
          weight: ''
        })
      },
      remove(){
        this.course.assignments.pop()
      },
      deleteCourse(){
        if(confirm("Are you sure that you want to remove the course " + this.course.name + " from the list?")){
          Event.$emit('delete-course', this.course.id)
        }
      },
      newInput(){
        Event.$emit('new-input', this.course)
      }
    }
  }
</script>

<style scoped>

</style>


